/* 배경 애니메이션 */
.container {
    width: 100%;
    height: 100vh;
    --s: 150px;
    --c-inner: rgb(174, 217, 150);
    --c-outer: #67b06f;
    --c-outer2: white;

    background: radial-gradient(
        circle at 50% 99%,
        transparent 10%,
        var(--c-inner) 10% 30%,
        var(--c-outer) 30% 60%,
        var(--c-outer2) 60% 100%,
        transparent 100%
    );
    background-size: var(--s) var(--s);
    animation: move 25s infinite linear;

    display: flex;
    justify-content: center;
    align-items: center;
}

/* 카드 스타일 */
.live-card {
    background: white;
    border-radius: 16px;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
    padding: 1.5rem;
    max-width: 700px;
    width: 100%;
    text-align: center;
}

.video-header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
}

.video-title {
    font-size: 1.5rem;
    font-weight: 600;
    color: #333;
}

.video-feed {
    width: 100%;
    border-radius: 12px;
    object-fit: cover;
}

/* 배경 애니메이션 keyframes */
@keyframes move {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 100% 0;
    }
}
